body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  color: #060b35;
}

.card {
  width: 200px;
  height: 406px;
  border-radius: 5px;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  padding: 32px 16px;

  &__img-container {
    display: flex;
    justify-content: center;
  }

  &__img {
    width: 160px;
    height: 134px;
  }

  &__text {
    margin-top: 40px;
    display: block;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    margin-bottom: 4px;
  }

  &__code {
    margin: 0;
    font-weight: 400;
    color: #616070;
    font-size: 10px;
    line-height: 14px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 16px;
  }

  &__stars {
    display: flex;
  }

  &__star {
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;

    &:last-child {
      margin-right: 0;
    }

    &--active {
      background-image: url(../images/star-active.svg);
    }
  }



  &__review {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;

    &-block {
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: #616070;
    }

    &-money {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
    }
  }

  &__button {
    margin-top: 16px;
    height: 40px;
    width: 100%;
    border-radius: 5px;
    border: none;
    background-color: #00acdc;
    font-weight: 700;
    color: #fff;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;

    &:hover {
      background-color: #fff;
      color: #00acdc;
      border: 1px solid #00acdc;
    }
  }
}
